/* -----------------------------------------------------------------------------
  Component: accordions

  Style for the accordion sections.
----------------------------------------------------------------------------- */
.section.accordion-parent {
  display: inline-block;
  width: 100%;
  margin-bottom: 0;
  margin-top: 0;
}

.accordion-title {
  display: flex;
  width: 100%;
  align-items: center;
  border-bottom: 2px solid;
  height: 1.5em;
}

.accordion-title::before {
  display: inline-block;
  width: 20px;
  height: 20px;
  margin-right: 10px;
  content: '';
  background-repeat: no-repeat;
  background-size: 20px 20px;
  background-position: center;
  transition: transform .2s;
}

.accordion-title.collapsed::before {
  transform: rotateZ(-90deg);
  transition: transform .2s;
}

/* Light theme -------------------------------------------------------------- */
body .accordion-title {
  border-bottom-color: #b7c6d7;
}

body .accordion-title::before {
  background-image: url("../../images/icons/svg-icons.svg#chevron-blue-lt");
}

/* Dark- theme -------------------------------------------------------------- */
.dark-theme .accordion-title {
  border-bottom-color: #e1e1e5;
}

.dark-theme .accordion-title::before {
  background-image: url("../../images/icons/svg-icons.svg#chevron-blue-dt");
}

/* -----------------------------------------------------------------------------
  Component: Admonitions

  Style for the different admonitions: note, warning, etc.
----------------------------------------------------------------------------- */
#central-column .admonition {
  padding: 0 20px 1px;
  margin-bottom: 2rem;
  border-left: 5px solid;
}

#central-column .admonition:not(.long) p {
  display: inline-block;
  margin-bottom: 0;
}

#central-column .admonition.long .admonition-title::after {
  vertical-align: middle;
  content: '';
  display: inline-block;
  width: 1.75rem;
  height: 1.75rem;
  position: absolute;
  left: 0;
  top: 18px;
}

#central-column .admonition.long.note .admonition-title, #central-column .admonition.long.warning .admonition-title, #central-column .admonition.long.attention .admonition-title, #central-column .admonition.long.caution .admonition-title, #central-column .admonition.long.danger .admonition-title, #central-column .admonition.long.error .admonition-title, #central-column .admonition.long.hint .admonition-title, #central-column .admonition.long.important .admonition-title, #central-column .admonition.long.tip .admonition-title {
  padding-left: 38px;
}

#central-column .admonition .admonition-title {
  padding: 16px 16px 16px 0;
  font-weight: 700;
  font-size: 18px;
  position: relative;
  line-height: 28px;
  margin-bottom: 0;
}

